import React, { useState, useEffect } from 'react';
import './App.css';
import About from './pages/about';
import Projects from './pages/projects';
import Home from './pages/home';
import sketch from './pages/sketch';
import P5Wrapper from 'react-p5-wrapper';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  HashRouter
} from "react-router-dom";

function App() {

  useEffect(() => {
    let vh = window.innerHeight * 0.01;
// Then we set the value in the --vh custom property to the root of the document
  document.documentElement.style.setProperty('–vh', vh + 'px');
    window.addEventListener('resize', () => {
      // We execute the same script as before
      let vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty('–vh', vh + 'px');
    })
  }, [])
  const [invert, setInvert] = useState(false);
  const classnames = [
    'App',
    invert && 'App--inverted'
  ].filter(Boolean);

  return (
    <HashRouter basename='/'>
      <div className={classnames.join(' ')} id="App">
          <P5Wrapper sketch={sketch} />
        <Switch>
          <Route path="/about">
            <header className="App-header">
              <h1 className={"App-category-title"}><Link to="/">Home</Link></h1>
              <h1 className={"App-category-title"}><Link to="/projects">Projects</Link></h1>
              <h1 className={"App-category-title-clicked"}>About</h1>
              <div className={"App-invert"} >
              <img src={invert ? "/assets/sun.png" : "/assets/moon.png"} onClick={()=>setInvert(!invert)}></img>
              </div>
            </header>
            <div className="App-body">
              <About />
            </div>
          </Route>
          <Route path="/projects">
            <header className="App-header">
              <h1 className={"App-category-title"}><Link to="/">Home</Link></h1>
              <h1 className={"App-category-title-clicked"}>Projects</h1>
              <h1 className={"App-category-title"}><Link to="/about">About</Link></h1>
              <div className={"App-invert"} >
              <img src={invert ? "/assets/sun.png" : "/assets/moon.png"} onClick={()=>setInvert(!invert)}></img>
              </div>
            </header>
            <div className="App-body">
              <Projects />
            </div>
          </Route>
          <Route exact path="/">
            <header className="App-header">
              <h1 className={"App-category-title-clicked"}>Home</h1>
              <h1 className={"App-category-title"}><Link to="/projects">Projects</Link></h1>
              <h1 className={"App-category-title"}><Link to="/about">About</Link></h1>
              <div className={"App-invert"} >
              <img src={invert ? "/assets/sun.png" : "/assets/moon.png"} onClick={()=>setInvert(!invert)}></img>
              </div>
            </header>
            <div className="App-body">
              <Home />
            </div>
          </Route>
        </Switch>
        <footer className="App-footer">
          <h1 className="App-category-title">© 2021</h1>
        </footer>
      </div>
    </HashRouter>
  );
}
export default App;