import p5 from 'p5';

export default function sketch(p) {
	let rotation = 0;
	var width = document.getElementById("App").offsetWidth;
	var height = document.getElementById("App").offsetHeight;

	let circle = [];
	let square = [];

	let morph = [];

	// This boolean variable will control if we are morphing to a circle or square
	let state = false;

	let img;
	p.preload = function () {
		img = p.loadImage('assets/cube.jpg');
	}

	p.setup = function () {
		p.createCanvas(width, height, p.WEBGL);

		// Create a circle using vectors pointing from center
		for (let angle = 0; angle < 360; angle += 9) {
			// Note we are not starting from 0 in order to match the
			// path of a circle.
			let v = p5.Vector.fromAngle(p.radians(angle - 135));
			v.mult(100);
			circle.push(v);
			// Let's fill out morph ArrayList with blank PVectors while we are at it
			morph.push(p.createVector());
		}

		// A square is a bunch of vertices along straight lines
		// Top of square
		for (let x = -50; x < 50; x += 10) {
			square.push(p.createVector(x, -50));
		}
		// Right side
		for (let y = -50; y < 50; y += 10) {
			square.push(p.createVector(50, y));
		}
		// Bottom
		for (let x = 50; x > -50; x -= 10) {
			square.push(p.createVector(x, 50));
		}
		// Left side
		for (let y = 50; y > -50; y -= 10) {
			square.push(p.createVector(-50, y));
		}
	};

	p.myCustomRedrawAccordingToNewPropsHandler = function (props) {
		if (props.rotation !== null) {
			rotation = props.rotation * Math.PI / 180;
		}
	};

	p.draw = function () {
		p.translate(0, 0, 0);
		p.background(255)
		p.push();
		p.rotateY(p.sin(p.frameCount * 0.001) * 10);
		p.rotateX(p.sin(p.frameCount * 0.001) * 10);
		p.texture(img);
		p.strokeWeight(0);
		p.stroke(255);
		p.box(window.innerWidth * p.sin(p.frameCount * 0.001), window.innerWidth * p.sin(p.frameCount * 0.001), window.innerWidth * p.sin(p.frameCount * 0.001));
		p.pop();
	};

	p.windowResized = function () {
		width = document.getElementById("App").offsetWidth;
		height = document.getElementById("App").offsetHeight;
		p.resizeCanvas(width, height);
	}
};